import React from 'react';
import { Button, OverlayTrigger, Popover } from 'react-bootstrap';

const popover = (
    <Popover id="popover-basic">
        <Popover.Content>
            Weź nie <strong>żartuj!</strong>
        </Popover.Content>
    </Popover>
);
const ButtonNews = () => (
    <OverlayTrigger trigger="click" placement="top" overlay={popover}>
        <Button variant="success">Zapisz się!</Button>
    </OverlayTrigger>

);
const Footer = () => {
    return (<>
        <footer className='bg-dark'>
            <div className="bg-secondary mt-5">
                <div className="container">
                    <div className="footCon">
                        <div className="footCon-div footCon-div-1 pt-3">
                            <h4 className="h5 text-light ml-4">BartekShop</h4>
                            <p className='text-light'>Podaj nam swój adres email, jeśli lubisz otrzymywać niechcianą korespondencję</p>
                        </div>
                        <div className="footCon-div ml-4 py-3">
                            <h4 className="h5 text-light ml-4">Newsletter</h4>
                            <div className="input-group">
                                <input className='p-1' type="email" placeholder='Twój adres @' />
                                <div className="input-group-append">
                                    <ButtonNews />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <p className='text-muted py-4 m-0 text-center'>copyright &copy; BartekShop</p>
            </div>
        </footer>

    </>);
}

export default Footer;